@import url(https://fonts.googleapis.com/css?family=Alegreya+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Anaheim&display=swap);
@import url(https://fonts.googleapis.com/css?family=Anaheim&display=swap);
body {
  background-color: black;
}

.home-description {
  color: white;
  font-size: 31.5px;
  font-family: 'Alegreya Sans', sans-serif;
}

.row-description {
  margin: 5%;
}

.img-description {
  border-radius: 2px;
  width: 100%;
}

.btn-clasic {
  background-color: rgb(94, 33, 104);
  border-radius: 0;
  color: rgb(243, 196, 56);
  font-family: 'Alegreya Sans', sans-serif;
  font-weight: bold;
  font-size: 25px;
  margin: 6% 0 6% 0;
  letter-spacing: 2px;
}

.btn-clasic:hover {
  background-color: rgb(77, 11, 87);
  color: white;

}

.img-logo {
  width: 50%;
}

.row-sagas > a {
  border: 1px solid transparent;
  text-align: center;

}
.row-sagas {
  justify-content: center;
}

.row-sagas > a:hover {
  border: 1px outset #FFDC74;
  border-radius: 8px;
}
.row-sagas {
  margin: 5% 0 5% 0;
}
.row-no-match {
  font-size: 30px;
  margin: 5%;
  color: white;
}
.movie-poster {
  width: 150px;
  border-radius: 8px;
  margin-bottom: 4%;
  border: solid black 6px;
}

.movie-title {
  background-color: black;
  text-align: center;
  padding: 5px;
  margin: 0;
  border-radius: 8px;
}

.movie-div {
  display: flex;
  flex-direction: column;
  color: white;
  margin: 8%;
  font-size: 20px;
  font-family: 'Alegreya Sans', sans-serif;
  font-weight: bolder;
  align-items: center;
  border: 1px indigo outset;
  border-radius: 20px;
  background-color: black;
}
.row-timeline {
  margin: 5%;
  color: white;
}

span {
  text-align: center;
  margin: 8%;
}
.timeline::before {
  content: "";
  z-index: -1;
  position: absolute;
  left: 50%;
  height: 100%;
  width: 5px;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(213, 213, 213, 0)), color-stop(35%, rgb(255, 255, 255)), to(rgba(213, 213, 213, 0)));
  background: -webkit-linear-gradient(top, rgba(213, 213, 213, 0) 0%, rgb(255, 255, 255) 35%, rgba(213, 213, 213, 0) 100%);
  background: linear-gradient(to bottom, rgba(213, 213, 213, 0) 0%, rgb(255, 255, 255) 35%, rgba(213, 213, 213, 0) 100%);
}

.release-title, .chrono-title, .start-title {
  text-align: center;
  font-family: 'Alegreya Sans', sans-serif;
  font-size: 35px;
  font-weight: bolder;
}

.release-title {
  color: #F6C1FF;
}

.chrono-title {
  color: #FFDC74;
}

.start-title {
  font-size: 25px;
}
.navbar-nav {
  width: 100%;
}

nav {
  font-family: 'Anaheim', sans-serif;
  font-weight: bolder;
  font-size: 20px;
  background-color: black;
  border-bottom: 1px solid #712B7C;
  margin-bottom: 30px;
}

.nav-item > .nav-link.active {
  border: 1px solid #FFDC74;
  border-radius: 8px;
  color:#FFDC74 !important;
}

.nav-justified .nav-item {
  margin: 20px 50px 20px 50px;
}

.navbar-dark .navbar-nav .nav-link {
  color: rgba(255, 255, 255, 0.815)
}

.nav-item:hover {
  border-radius: 8px;
}

a.nav-link:hover {
  color: white !important;
}

.show > div > .nav-item:hover {
  background-color: black !important;
}

.navbar-toggler {
  background-color:  #712B7C;
  border: 5px solid transparent
}

